body {
  background-color: white;
}

.center {
  display: grid;
  place-items: center;
  min-height: 100vh;
  margin: 0;
}

.flex {
  display: flex;
  gap: 2rem;
}

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

body {
  background-color: powderblue;
}

.pokeball {
  width: 20rem;
  height: 20rem;
  border-radius: 50%;
  position: relative;
  background-image: linear-gradient(#ff5a5a 0%, #ff5a5a 50%, white 50%, white 100%);
}
.pokeball::before {
  content: "";
  position: absolute;
  top: 50%;
  height: 20px;
  width: 100%;
  border: 10px, solid, black;
}
.pokeball::after {
  content: "";
  position: absolute;
  top: 43%;
  left: 42%;
  height: 4rem;
  width: 4rem;
  border-radius: 50%;
  border: 10px, solid, black;
}